.gallery{
  background-image: linear-gradient(to top,rgba(30,30,30,0.90),rgba(30,30,30,0.6),rgba(255, 255, 255, 0.6)),url(./building1.jpg);
  background-repeat: no-repeat;
  background-size:cover;
  background-position: center;
  height: 100vh;
}
.gallery main{
  padding-top: 90px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.gallery main .left{
  max-width: 850px;
  text-align: center;
  margin-top: 100px;
  letter-spacing: 1.2px;
}
.gallery main .left h1{
  font-size: 35px;
  padding: 10px 0;
  font-family: "Dancing Script", cursive;
  font-optical-sizing:auto;
}
.gallery main .left p{
  font-size: 20px;
  padding: 5px 0px;
  backdrop-filter: blur(1px);
  border-radius: 10px;
  margin-bottom: 30px;
}
.gallery main .left a{
  background-color: rgb(183, 140, 0);
  color: white;
  padding: 12px 24px;
  border-radius:5px;
  transition: 0.5s ease;
}
.gallery main .left a:hover{
  background-color: rgb(214, 0, 0);
  border-radius: 20px;
}
@media(max-width:786px){
  .gallery main .left{
      padding-left: 20px;
      margin-top: 10px;
  }
  .gallery main .left h1{
      font-size: 20px;
      margin-top: 70px;
  }
  .gallery main .left p{
      font-size: 18px;
  }
}
.custom-lightgallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(450px, 1fr));
  gap: 10px;
}

.custom-lightgallery a {
  position: relative;
  overflow: hidden;
  border-radius: 8px;
  display: block;
  width: 100%;
  padding-bottom: 75%; /* This helps maintain aspect ratio */
}

.custom-lightgallery img {
  position: absolute;
  top: 0;
  left: 0;
  width:100%;
  height: auto;
  object-fit:contain;
  transition: transform 0.3s ease;
  border-radius: 8px;
}

.custom-lightgallery a:hover img {
  transform: scale(1.05); /* Slight zoom effect on hover */
}
